#nav{
    display: flex;
    align-items: center;
   padding: 15px 30px;
   justify-content: space-between;
   background-color: white;
        /* box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.25); */
        box-shadow: 12px 0 15px -4px rgba(31, 73, 125, 0.8),
        -12px 0 8px -4px rgba(31, 73, 125, 0.8);
      box-shadow: 0 9px 0px 0px white, 0 -9px 0px 0px white,
        12px 0 15px -4px rgba(31, 73, 125, 0.8),
        -12px 0 15px -4px rgba(31, 73, 125, 0.8)

      
   
   }
   
   #nav>img{
     width: 40px;
     height: 40px;
   }
#nav #right{

    display: flex;
    align-items: center;
    gap: 30px;
    
}
 a{

   text-decoration: none;
    color: black;
    font-family:'roboto',sans-serif;
    font-weight: 400;
    
 }
 a:before,
a:after {
  content: "";
  position: absolute;
  height: 3px;
  width: 0;
  background-color: #010302;
  transition: 0.5s;
}
a:after {
  left: 0;
  bottom: -10px;
}
a:before {
  right: 0;
  top: -10px;
}
a:hover {
  color: #bb01ff;
}
.nav-link:hover,
.nav-link:active {
  border-bottom: 2px solid #1bcfc6;
}
a:hover:after,
a:hover:before {
  width: 100%;
}