#main_home{
    margin-top: 20px;;
    display: flex;
    flex-direction: column;
align-items: center;
padding: 8vw 12vw;
background-color: rgb(255, 255, 255);
margin-bottom: 0;

}

#mainHeading{
    display: flex;
    gap:5px;
}

  

h1{
    font-size:1.5vw;
    font-family: 'AvertaCY-Semibold';
  
    margin: 20px;

}

    
#mainHeading h1{
    font-size: 5vw;
    font-family: 'AvertaCY-bold';
font-weight: 900;
letter-spacing: -2px;
line-height: 1px;
color: transparent;
-webkit-text-stroke: 1.5px black;

}
#mainHeading h1:hover{
    font-size: 5vw;
    font-family: 'AvertaCY-bold';
font-weight: 900;
letter-spacing: -2px;
line-height: 1px;
color: black;


}
#homeLast
{
    margin-top: 2px;
font-size: 1.4vw;
font-family: AvertaCY-Regular;
font-weight:400;
letter-spacing: 0.001vw;
padding: 8vw 12vw;
}
#homeLast p::selection{
    color: white;
    background-color: black;
}


