
.heading{

    padding: 4vw 15vw;
    
   
}

.heading>h1{
font-weight: 300;
line-height: 0.8;
font-size: 6vw;
}
.heading>h1::selection{
    color: white;
    background-color: black;
}

.heading>h1:nth-child(2){
color: transparent;
-webkit-text-stroke: 1px black;

}
.projectSamples{

margin-top: 3%;
height: 100vh;
display: flex;
padding-left: 8vw;
padding-right: 8vw;
justify-content: space-between;
}


#PSleft{
height: inherit;
width: 45%;
overflow: hidden;

}

.leftElm
{
    width: 100%;
    height: inherit;
    display: flex;
    flex-direction: column;
   justify-content: center;
}
.leftElm>h1{
    padding: 0;
   
    font-size: 2vw;
    font-weight: 800px;
    font-family: AvertaCY-Bold;
    margin-top: 0px;
    margin-bottom: 0px;
    
}
.leftElm>h2{
    margin-left: 20px;
    margin-bottom: 0px;
    font-family: AvertaCY-Regular;
    font-size: 0.8vw;
}

.leftElm>h3{
    margin-bottom: 0px;
    margin-left: 20px;
    margin-top: 0px;
    font-size: 0.8vw;
    font-family: AvertaCY-Regular;
}

#PRight{
  height: inherit;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 45%;
    
    }

    #Window{
        width: 90%;
        height: 100%;
        background-color: rgb(14, 255, 94);
    }

    #view-button{
display: flex;
align-items: center;
}