

.App {
  width: 100%;
  background-color: rgb(255, 255, 255) ;
  
}
@font-face {
  font-family: 'AvertaCY-Bold';
  src: local('AvertaCY-Bold'), url(./fonts/AvertaCY-Bold.ttf) format('truetype');
}

@font-face {
  font-family: 'AvertaCY-Regular';
  src: local('AvertaCY-Regular'), url(./fonts/AvertaCY-Regular.ttf) format('truetype');
}

@font-face {
  font-family: 'AvertaCY-Light';
  src: local('AvertaCY-Light'), url(./fonts/AvertaCY-Light.ttf) format('truetype');
}

@font-face {
  font-family: 'AvertaCY-Semibold';
  src: local('AvertaCY-Semibold'), url(./fonts/AvertaCY-Semibold.ttf) format('truetype');
}
#main{
  width: 100%;
  min-height: 100vh;
  
}
.hidden {
  opacity: 0;
  filter: blur (5px);
  transform: translateX(-100);
  transition: all 1s;
  }


  .show {
  opacity: 1;
  filter: blur(0);
  transform: translateX(0);
  }



